/**
 * ThemeProvider is playing a very important role in AF FED architecture.
 * All applications would get rendered inside a shadow-dom, we need to support it via 2 actions:
 * 1. Make sure JSS inject all style tags to the theme provider's root instead of the default <head/> tag.
 * 2. Make sure all modals and popover components get injected to the theme provider's root, instead of document.body
 *
 */
import React, { forwardRef, useLayoutEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import { commonThemeSettings } from './materialTheme';
import { create } from 'jss';
import {
  jssPreset,
  StylesProvider,
  createGenerateClassName,
  createMuiTheme,
  ThemeProvider as MuiThemeProvider
} from '@material-ui/core/styles';
import FontsLoader from './FontsLoader';

const generateClassName = createGenerateClassName({
  productionPrefix: 'AF_DS'
});

const InsertionPointElement = forwardRef((props, ref) => (
  <noscript
    data-comment="insertion point for jss styles"
    {...props}
    ref={ref}
  />
));

const applyRootContainerToThemeProps = container => ({
  MuiPopover: {
    container
  },
  MuiModal: {
    container,
    disableEnforceFocus: true // fixes a bug see https://github.com/mui-org/material-ui/issues/24032#issuecomment-755873514
  },
  MuiDrawer: {
    container
  },
  MuiTooltip: {
    PopperProps: {
      container
    }
  }
});

const ThemeProvider = ({
  children,
  shouldUseGlobalInsertionPoint,
  insertionPointElement,
  ...rest
}) => {
  // lazy init with the MUI default JSS preset
  const [defaultMuiJSSPreset] = useState(() => jssPreset());
  const [theme, setTheme] = useState();
  const rootRef = useRef();
  const insertionPointRef = useRef(
    insertionPointElement || defaultMuiJSSPreset.insertionPoint
  );
  const [customJSSObj, setJss] = useState();

  // create a point element only when:
  // 1. The consumer didn't pass an insertionPoint element
  // 2. The consumer didn't explicitly asked for the default insertionPoint
  const shouldRenderInsertionPoint =
    !insertionPointElement && !shouldUseGlobalInsertionPoint;

  const isReadyToRender = customJSSObj && theme;

  useLayoutEffect(() => {
    if (shouldUseGlobalInsertionPoint) {
      setJss(defaultMuiJSSPreset);
      setTheme(createMuiTheme(commonThemeSettings));
    } else {
      setJss(() => {
        return {
          ...defaultMuiJSSPreset,
          // We define a custom insertion point that JSS will look for injecting the styles in the DOM.
          // At this point it should be either the passed in element OR the ref to our own created element
          insertionPoint: insertionPointRef.current
        };
      });
      // We extend (and override) commonThemeSettings, so we can set a root for all modals, tooltips etc.
      // We need this because we might get rendered inside a shadow-dom and we don't want to get attached to document.body.
      // Note: order matters, 2nd argument of createMuiTheme overrides the first.
      setTheme(
        createMuiTheme(commonThemeSettings, {
          props: applyRootContainerToThemeProps(rootRef.current)
        })
      );
    }
    // we don't need deps as we only want to run this effect once.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div ref={rootRef} data-id="af-theme-root" {...rest}>
      {shouldRenderInsertionPoint && (
        <InsertionPointElement ref={insertionPointRef} />
      )}
      <FontsLoader />
      {isReadyToRender && (
        <StylesProvider
          jss={create(customJSSObj)}
          generateClassName={generateClassName}>
          <MuiThemeProvider theme={theme}>{children}</MuiThemeProvider>
        </StylesProvider>
      )}
    </div>
  );
};

ThemeProvider.defaultProps = {
  shouldUseGlobalInsertionPoint: true
};

ThemeProvider.propTypes = {
  children: PropTypes.node.isRequired,
  /** Our ThemeProvider is configured to insert the stylesheets inside the root of the render tree,
   * you can set it to use the default insertion point which is the <head/> element
   * Note that this will also make all modals, tooltips and popovers injected to document.body instead of the root.
   * */
  shouldUseGlobalInsertionPoint: PropTypes.bool,
  /** You can specify an insertion point to render the stylesheets (a string or ref to a DOM node)
   * More details in the DOCS
   * https://github.com/cssinjs/jss/blob/master/docs/setup.md#specify-the-dom-insertion-point
   */
  insertionPointElement: PropTypes.oneOfType([
    PropTypes.instanceOf(HTMLElement),
    PropTypes.string
  ])
};

export default ThemeProvider;
