import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';
import { fade } from '@material-ui/core/styles';
import { muiPalette } from './muiPalette';

const ValueLabelComponent = ({ children, open, value }) => {
  return (
    <Tooltip open={open} enterTouchDelay={0} placement="top" title={value}>
      {children}
    </Tooltip>
  );
};

const SIZES = {
  small: 240,
  medium: 360,
  large: 500
};

const DEFAULT_SIZE = SIZES.medium;

export const sliderOverride = {
  props: {
    MuiSlider: {
      ValueLabelComponent
    }
  },
  theme: {
    MuiSlider: {
      root: {
        width: props => SIZES[props.size] || DEFAULT_SIZE
      },
      track: {
        height: 4
      },
      rail: {
        height: 4,
        opacity: 1,
        backgroundColor: fade(muiPalette.palette.grey[900], 0.1)
      },
      mark: {
        height: 4,
        width: 4,
        backgroundColor: fade(muiPalette.palette.background.paper, 0.6)
      },
      markActive: {
        backgroundColor: 'transparent'
      },
      markLabel: {
        transform: 'translateX(-35%)'
      }
    }
  }
};
